import React from 'react';
import styles from '../../assets/styles/LayoutContenidoMuniComuna.module.css';
import historia from '../../assets/static/comuna/hist3.jpg';

const Historia = () => {
  const contenidoA = `
    La comuna de Nogales naci como denominacin geogrfica,
    en el lugar correspondiente a la Sptima Subdelegacin de Quillota, el 2 de Noviembre de 1878
    , cuando don Jos del Carmen Godoy Andreu en comunidad con su hermano Pedro Eleuterio 
    adquirieron de la sucesin de los hermanos Corts, parte de la hacienda Los Litres.

    En principio los seores Godoy destinaron estas tierras a la agricultura,
    sin ms casas que las ocupadas por ellos y las indispensables para los 
    agricultores que necesitaban en sus labores; el conjunto de este predio 
    se denomin NOGALES, por existir un rbol de esta especie muy frondoso
    que se arraigaba a la vera del camino pblico a unos 30 Mts. al Norte de
    la actual iglesia por la acera del frente.`;
  const contenidoB = `
    Este nombre lo absorbi la hacienda Los Nogales, que debido al gran auge comercial 
    y minero que tom, hizo crecer rpidamente la poblacin y se hizo necesario la creacin 
    de correos y telfonos que pasaron a llevar el nombre de Los Nogales como subdelegacin 
    15 del depto. de Quillota, con una poblacin mayor al millar de habitantes. 
    Desde su origen la comuna ha ido paulatinamente evolucionando, cobrando especial 
    relieve al auge minero que naci con la explotacin de los minerales El Cobre y Minas Navo,
    lo cual fue dando un mayor impulso a su crecimiento, aparte de la explotacin del agro que
    en conjunto conforman las riquezas bsicas de la comuna.

    En 1876 se descubren otras minas de cobre, adems de la ya explotada caliza.
    Para ese entonces exista una poblacin de 1500 personas aproximadamente,
    de ese modo el meln ira tomando identidad propia entre cerros desde donde
    se ejecutan las faenas. A comienzos de siglo (1906), se inicia una nueva
    etapa para esta zona, pues nace la empresa cemento meln, que dio trabajo
    a una gran cantidad de personas, con lo que la poblacin aument y se estabiliz.
    As, como en muchas zonas mineras el poblamiento y la ocupacin de terrenos fue
    espontnea orientndose hacia los cerros y quebradas. Dentro de este marco histrico
     cultural se hace relevante mencionar a la Sra. Isabel Brown quien fue pieza clave
    en el desarrollo de la comuna en los comienzos de siglo, colaborando activamente
    con la comunidad hasta la fecha de su muerte, en 1942, a ella se deben innumerables
    adelantos en materias educativas y religiosas. Entre sus obras se destacan la
    Parroquia Santa Isabel de Hungra (Declarada Monumento Histrico Nacional y su escuelita
    hoy Liceo Felipe Cortes`;
  return (
    <div className={styles.muniContainer}>
      <h1 className={styles.muniTitle}>HISTORIA</h1>
      <img className={styles.imgMunicipalidad} src={historia} alt='frontis de municipalidad' />
      <p>{contenidoA}</p>
      <p className={styles.bottomParraph}>{contenidoB}</p>

    </div>
  );
};
export default Historia;
